import React from 'react';
import {HashRouter,BrowserRouter , Switch ,Route} from 'react-router-dom';
import Home from './components/Home'
import Input20 from './components/Inputs/Input20'
import Input1 from './components/Inputs/Input1'
import Input2 from './components/Inputs/Input2'
import Input3 from './components/Inputs/Input3'
import Input4 from './components/Inputs/Input4'
import Input5 from './components/Inputs/Input5'


function App() {
  return (
    <HashRouter>
      <Switch>
        <Route exact path='/' component={Home}/>
        <Route exact path="/1" component={Input1}/>
        <Route exact path="/2" component={Input2}/>
        <Route exact path="/3" component={Input3}/>
        <Route exact path="/4" component={Input4}/>
        <Route exact path="/5" component={Input5}/>

        <Route exact path="/20" component={Input20}/>
      </Switch>
    </HashRouter>
    
  );
}

export default App;
